@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;600;700&display=swap');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header{
  position: fixed;
  top:0; left: 0; right: 0;
  z-index: 10000;
  background: #333;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding:1.5rem 9%;
}


:root{
    --main-color:#3867d6;
}

*{
    font-family: 'Nunito', sans-serif;
    margin:0; padding:0;
    box-sizing: border-box;
    outline: none; border:none;
    text-decoration: none;
    text-transform: capitalize;
    transition: .2s linear;
}

html{
    font-size: 62.5%;
    overflow-x: hidden;
    scroll-padding-top: 7rem;
    scroll-behavior: smooth;
}

html::-webkit-scrollbar{
    width:1rem;
}

html::-webkit-scrollbar-track{
    background: #444;
}

html::-webkit-scrollbar-thumb{
    background: var(--main-color);
    border-radius: 5rem;
}

body{
    background: #222;
}

section{
    padding:2rem 9%;
}

.heading{
    text-align: center;
    padding-bottom: 2rem;
    color:#fff;
    text-transform: uppercase;
    font-size: 4rem;
}

.heading span{
    color:var(--main-color);
    text-transform: uppercase;
}

.btn{
    margin-top: 1rem;
    display: inline-block;
    padding:.8rem 3rem;
    font-size: 1.7rem;
    border-radius: .5rem;
    background: #666;
    color:#fff;
    cursor: pointer;
    font-weight: 600;
}

.btn:hover{
    background:var(--main-color);
}
.header .logo{
  font-weight: bolder;
  color:#fff;
  font-size: 2.5rem;
}

.header .logo span{
  color:var(--main-color);
}

.header .navbar a{
  font-size: 1.7rem;
  color:#fff;
  margin-left: 2rem;
}

.header .navbar a:hover{
  color:var(--main-color);
}
.footer{
  background:#111;
  padding-top: 400px;
}

.footer .box-container{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  gap:1.5rem;
}

.footer .box-container .box h2{
  font-size: 2.5rem;
  padding:1rem 0;
  color:green;
}

.footer .box-container .box a{
  display: block;
  font-size: 1.5rem;
  padding:1rem 0;
  color:#eee;
}

.footer .box-container .box a i{
  padding-right: .5rem;
  color:var(--main-color);
}

.footer .box-container .box a:hover i{
  padding-right: 1.5rem;
  color:#fff;
}

.footer .credit{
  text-align: center;
  border-top: .1rem solid #222;
  color:#fff;
  padding:2rem;
  padding-top: 2.5rem;
  margin-top: 1rem;
  font-size: 2rem;
}

.footer .credit span{
  color:var(--main-color);
}

.theme-toggler{
  position: fixed;
  top:10rem; right:-20rem;
  background: #333;
  z-index: 1000;
  width: 20rem;
  text-align: center;
}

.theme-toggler.active{
  right:0;
}

.theme-toggler h2{
  color:#fff;
  padding:1rem 0;
  font-size: 2rem;
}

.theme-toggler .buttons{
  display: flex;
  justify-content:center;
  flex-wrap: wrap;
  gap:1rem;
  padding:1rem;
}

.theme-toggler .buttons .theme-btn{
  height: 5rem;
  width: 5rem;
  border-radius: 50%;
  cursor: pointer;
}

.theme-toggler .toggle-btn{
  position: absolute;
  top:0; left:-5.9rem;
  padding:1.3rem 1.5rem;
  background:#333;
  cursor: pointer;
}

.theme-toggler .toggle-btn i{
  color:#fff;
  font-size: 3rem;
  animation: spin 4s linear infinite;
}
